/**
  * Basic styles for links
  */
a {
  text-decoration: none; }

html::before {
  bottom: 0;
  content: "";
  display: block;
  left: 500px;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 0; }

body {
  background-color: white;
  color: #050505;
  font: 62.5% franklin-gothic-urw, -apple-system, BlinkMacSystemFont, sans-serif;
  line-height: 1.5;
  width: 1250px;
  display: grid;
  grid-template-columns: repeat(10, 100px);
  column-gap: 25px;
  margin-bottom: 100px; }

main {
  position: relative;
  z-index: 2; }

/*
 * Using 5 sizes based on perfect fifth scale (2:3) using 20px as base size. 
 * Scale generated using https://www.modularscale.com/
*/
h1,
.text-size-xxl {
  font-size: 101.25px;
  font-weight: 700;
  letter-spacing: -3px;
  line-height: 1; }

h2,
.text-size-xl {
  font-size: 67.5px;
  font-weight: 700;
  letter-spacing: -2px;
  line-height: 1; }

h3,
.text-size-l {
  font-size: 45px;
  font-weight: 700;
  letter-spacing: -1px;
  line-height: 1; }

.text-size-s {
  font-size: 20px;
  line-height: 1.5; }

h4 {
  font-size: 30px;
  font-weight: 700;
  margin-top: 10px;
  line-height: 1.1; }

.text-style-5 {
  color: #858585;
  font-size: 20px;
  letter-spacing: 5px;
  line-height: 1.5;
  margin-top: 15px;
  text-transform: uppercase; }

p,
.text-style-4 {
  font-size: 20px;
  line-height: 1.5;
  margin-top: 15px; }

.type-serif {
  font-family: ff-scala-web, serif;
  font-weight: 400;
  font-style: normal; }

.u-mar-left--more {
  transform: translateX(600px);
  width: 650px; }

.u-mar-left--less {
  transform: translateX(100px); }

.c-entry {
  margin-top: 100px; }

.c-entry__blurb {
  border-left: 6px double #c5c5c5;
  display: block;
  margin-top: 15px;
  padding-left: 50px; }

.c-name {
  color: #c5c5c5;
  display: inline-block;
  font-size: 30px;
  font-weight: bold;
  grid-column-start: 1;
  grid-column-end: 5;
  position: relative;
  text-transform: uppercase; }
  .c-name::before {
    content: "";
    display: block;
    position: absolute;
    left: -9999px;
    right: 0; }

.c-nav__link {
  align-items: center;
  display: flex;
  justify-content: center;
  height: 100px;
  width: 100px; }
  .c-nav__link.is-active .c-nav__icon {
    fill: #050505; }

.c-nav__icon {
  fill: #858585; }

.c-wrapper {
  position: relative;
  display: grid;
  grid-template-columns: repeat(5, 150px);
  grid-column-gap: 75px;
  grid-row-gap: 75px;
  width: 1250px;
  z-index: 2; }

.c-book__cover {
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.15);
  width: 150px; }

.c-section__heading {
  color: #858585;
  position: sticky;
  padding-left: 125px;
  line-height: 1;
  top: 50px;
  margin-top: 100px; }
  .c-section__heading::before {
    background-color: #858585;
    content: "";
    display: block;
    height: 10px;
    left: 0;
    position: absolute;
    top: 12px;
    width: 140px; }

.l-grid {
  display: grid;
  grid-column: 1/-1;
  column-gap: 25px;
  grid-template-columns: repeat(10, 100px); }

.l-grid--header {
  align-items: center;
  grid-template-areas: "name name name name . nav nav nav nav nav";
  grid-template-rows: 100px; }
  .l-grid--header .c-name {
    grid-area: name; }
  .l-grid--header .c-nav {
    grid-area: nav; }

.l-grid--billboard {
  grid-column: 1/-1;
  grid-auto-flow: row;
  grid-template-rows: auto auto auto; }
  .l-grid--billboard .c-billboard__title {
    grid-column: 1/4; }
  .l-grid--billboard .c-billboard__blurb {
    grid-column: 3/10; }
  .l-grid--billboard .c-billboard__link {
    grid-column: 3/4; }

.l-grid--section .c-section__heading {
  grid-column: 1/-1; }

.l-grid--section .c-section__content {
  grid-column: 6/-1;
  margin-top: 50px; }

.l-grid--section .c-section__col {
  grid-column: 3/5; }

.c-billboard {
  background: #fff;
  grid-column-start: 1;
  grid-column-end: 11;
  padding: 200px 100px;
  margin: 0 0 100px -100px;
  position: relative;
  z-index: 9; }

.c-billboard__title {
  padding-left: 125px;
  position: relative;
  text-transform: uppercase; }
  .c-billboard__title::before {
    content: "";
    display: block;
    position: absolute;
    height: 15px;
    width: 50%;
    left: 0;
    top: 50%;
    transform: translate(0, -50%);
    background-color: #050505; }

.p-about .c-billboard__title::before {
  top: 64px; }

.c-billboard__blurb {
  font-size: 45px;
  font-weight: bold;
  color: #454545;
  letter-spacing: -1px;
  line-height: 1.1; }

.c-entry {
  margin-bottom: 100px; }

.c-entry__link {
  border-left: 6px double #c5c5c5;
  color: #858585;
  display: block;
  margin-top: 30px;
  padding: 15px 30px 25px 50px; }
  .c-entry__link:hover {
    background: white;
    border-style: solid; }

.c-entry__embed {
  margin: 50px 0 0 55px; }

.c-entry__author {
  color: #858585;
  font-size: 20px;
  letter-spacing: 5px;
  line-height: 1.5;
  margin-top: 15px;
  text-transform: uppercase; }

.c-name {
  position: relative;
  padding-left: 125px;
  line-height: 1; }
  .c-name::before {
    background-color: #c5c5c5;
    content: "";
    display: block;
    height: 5px;
    left: 0;
    position: absolute;
    top: 20px;
    width: 27%; }

.p-home::before {
  background-color: #e4f9ff; }

.p-home .c-billboard {
  background-color: #ffe000; }

.p-about::before {
  background-color: #eaffd0; }

.p-about .c-billboard {
  background-color: #ff7315; }
